/* Extends and mixins go here */
/* Create extendable styles for a flex-styled list, horizontal by default. */
.app-main-nav ul, .things-I-love {
  display: flex;
  justify-content: space-between;
  padding: 0; }
  .app-main-nav ul li, .things-I-love li {
    display: unset;
    margin: 0.6rem 0;
    padding: 1rem; }

/* Import from partials (styles seperated by type/section) */
/* Basic content level html elements go here */
/* More general layout styling */
#root > section {
  padding: 5%; }

/* Styles for the app header */
.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }
  .app-header h1 {
    margin-bottom: 0; }
    .app-header h1 + p {
      margin-top: 0.5rem; }
  .app-header a {
    color: white; }

/* Styles specific to the about section */
.things-I-love {
  flex-wrap: wrap; }
  .things-I-love li {
    background: #efefef; }

/* General/simple classes */
.large {
  font-size: 165%;
  line-height: 0;
  vertical-align: sub; }

